import axios from 'axios'
import iView from 'iview'
import 'iview/dist/styles/iview.css'

axios.interceptors.request.use(config => {
  //显示loading
  iView.Spin.show()
  return config
}, error => {
  return Promise.reject(error)
})

//响应拦截器即异常处理
axios.interceptors.response.use(response => {
  iView.Spin.hide()
  return response
}, err => {
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        err.message = '错误请求'
        break;
      case 401:
        err.message = '未授权，请重新登录'
        break;
      case 403:
        err.message = '拒绝访问'
        break;
      case 404:
        err.message = '请求错误,未找到该资源'
        break;
      case 405:
        err.message = '请求方法未允许'
        break;
      case 408:
        err.message = '请求超时'
        break;
      case 500:
        err.message = '服务器端出错'
        break;
      case 501:
        err.message = '网络未实现'
        break;
      case 502:
        err.message = '网络错误'
        break;
      case 503:
        err.message = '服务不可用'
        break;
      case 504:
        err.message = '网络超时'
        break;
      case 505:
        err.message = 'http版本不支持该请求'
        break;
      default:
        err.message = `连接错误${err.response.status}`
    }
  } else {
    err.message = "连接到服务器失败"
  }
  iView.Spin.hide()
  iView.Modal.error({
    title: "警告",
    scrollable: true,
    content: err.message
  })
  return Promise.resolve(err.response)
})

function errorState(response) {
  //隐藏loading
  iView.Spin.hide()
  // 如果http状态码正常，则直接返回数据
  if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
    console.log(response)
    return response
    // 如果不需要除了data之外的数据，可以直接 return response.data
  }
}

function successState(res) {
  //统一判断后端返回的错误码
  iView.Spin.hide()
  if(res.data.code > 0){
    iView.Modal.error({
      title: "警告",
      scrollable: true,
      content: res.data.code
    })
  }
}
const httpServer = (opts, data) => {

  let Public = { //公共参数
    tokenid: sessionStorage.getItem('tokenid') === 'undefined' ? '': sessionStorage.getItem('tokenid')
  }

  let httpDefaultOpts = { //http默认配置
    method: opts.method,
    url: opts.url,
    timeout: 10000,
    data: Object.assign(Public, data),
    headers: {
      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
    }
  }

  let promise = new Promise(function (resolve, reject) {
    axios(httpDefaultOpts).then(
      (res) => {
        successState(res)
        resolve(res)
      }
    ).catch(
      (response) => {
        errorState(response)
        reject(response)
      }
    )

  })
  return promise
}

export default httpServer


