<template>
  <div>
    <Header></Header>
    <i-content>
      <div class="login_content" style="height: 798px;">
        <div class="login_content_wrap">
          <div class="login_content_wrap_left">
            <div class="login_name">欢迎登录</div>
            <div class="login_inp_warp">
              <div class="login_inp_top">邮箱</div>
              <Input class="login_inp_sty" type="email" size="large" clearable v-model="email"/>
            </div>
            <div class="login_inp_warp">
              <div class="login_inp_top">密码</div>
              <Input
                class="login_inp_sty"
                type="password"
                size="large"
                clearable
                v-model="password"
              />
            </div>

            <div class="login_inp_warp">
              <div class="login_inp_top">验证</div>
              <div id="embed-captcha"></div>
              <p id="wait" class="show">正在加载验证码......</p>
              <p id="notice" class="hide">请先完成验证</p>
            </div>

            <div class="forget">
              <router-link to="/">忘记密码</router-link>
            </div>

            <Button
              class="login_submit"
              type="primary"
              :loading="loading"
              size="large"
              id="submitBtn"
              @click="submitBtn()"
            >
              <span v-if="!loading">登录</span>
              <span v-else>Loading...</span>
            </Button>
          </div>
        </div>
      </div>
    </i-content>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "./public/Header";
import Footer from "./public/Footer";
import gt from "../assets/js/gt";
export default {
  name: "Login",
  data() {
    return {
      loading: false,
      email: "",
      password: "",
      geetest_challenge: "1",
      geetest_validate: "",
      geetest_seccode: ""
    };
  },
  components: {
    Header,
    Footer
  },
  methods: {
    submitBtn: function() {
      const emailVerify = this.$store.state.emailVerify;
      if (!this.email.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请先填写邮箱"
        });
      } else if (!emailVerify.test(this.email)) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请检查邮箱格式"
        });
      } else if (!this.password.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请填写您的密码"
        });
      } else {
        this.loading = true;
        this.geetest_challenge = $(
          ".geetest_form input[name^='geetest_challenge']"
        ).val();
        this.geetest_validate = $(
          ".geetest_form input[name^='geetest_validate']"
        ).val();
        this.geetest_seccode = $(
          ".geetest_form input[name^='geetest_seccode']"
        ).val();
        this.http(this.ApiSetting.login, {
          email: this.email,
          password: this.password,
          geetest_challenge: this.geetest_challenge,
          geetest_validate: this.geetest_validate,
          geetest_seccode: this.geetest_seccode
        }).then(
          res => {
            this.loading = false;
            if (res.data.code === 0) {
              this.$Modal.error({
                title: "提示",
                scrollable: true,
                content: "登录成功"
              });
              this.$store.state.token = res.data.data.token;
              sessionStorage.token = res.data.data.token;
              let GetQueryString = this.$route.query.route;
              if (GetQueryString) {
                this.$router.push({ path: GetQueryString });
              } else {
                this.$router.push({ path: "/" });
              }
            } else if (res.data.code === 100000) {
              this.$Modal.error({
                title: "提示",
                scrollable: true,
                content: "请先完成验证"
              });
            }
          },
          error => {
            console.log(error);
          }
        );
      }
    },
    handlerEmbed: function(captchaObj) {
      let _this = this;
      captchaObj.appendTo("#embed-captcha");
      captchaObj.onReady(function() {
        $("#wait")[0].className = "hide";
      });
    }
  },
  mounted() {
    this.http(this.ApiSetting.loginVerification).then(
      res => {
        const data = res.data.data;
        console.log(data);
        initGeetest(
          {
            gt: data.gt,
            width: "100%",
            challenge: data.challenge,
            new_captcha: data.new_captcha,
            product: "embed", // 产品形式，包括：float，embed，popup。注意只对PC版验证码有效
            offline: !data.success // 表示用户后台检测极验服务器是否宕机，一般不需要关注
          },
          this.handlerEmbed
        );
      },
      error => {
        console.log(error);
      }
    );

    this.$store.state.activename = "2";

    let _this = this;
    // 将键盘事件 绑定在document之上
    document.onkeydown = function(e) {
      var key = window.event.keyCode;
      if (key == 13) {
        _this.submitBtn();
      }
    };

    // 判断是否已经登录
    let sess = sessionStorage.getItem("token");
    if (sess !== null) {
      let _this = this;
      this.$Modal.error({
        title: "警告",
        scrollable: true,
        content: "您已登录，请不要重复登录",
        onOk: function() {
          let GetQueryString = _this.$route.query.route;
          if (GetQueryString) {
            _this.$router.push({ path: GetQueryString });
          } else {
            _this.$router.push({ path: "/" });
          }
        }
      });
    }
  }
};
</script>

<style scoped>
@media screen and (max-width: 900px) {
  .login_content_wrap {
    width: 89.5% !important;
  }
}

>>> .geetest_holder.geetest_wind .geetest_radar_btn {
  background-image: none;
}

>>> .geetest_holder.geetest_wind .geetest_radar_btn,
.geetest_holder.geetest_wind .geetest_success_btn {
  border: 1px solid #dcdee2;
  border-radius: 4px;
}

>>> .geetest_holder.geetest_wind .geetest_success_btn .geetest_success_box {
  width: 23px;
  height: 23px;
  top: 6px;
}

>>> .geetest_holder.geetest_wind .geetest_logo,
.geetest_holder.geetest_wind .geetest_success_logo {
  top: 8px;
}

>>> .geetest_holder.geetest_wind .geetest_radar {
  width: 23px;
  height: 23px;
}

>>> .geetest_holder.geetest_wind {
  height: 37px;
}

>>> .geetest_holder.geetest_wind .geetest_radar_tip,
.geetest_holder.geetest_wind .geetest_success_radar_tip {
  height: 37px;
  line-height: 37px;
}

>>> .geetest_holder.geetest_wind .geetest_logo,
.geetest_holder.geetest_wind .geetest_success_logo {
  width: 18px;
  height: 18px;
  top: 10px;
}

>>> .geetest_holder.geetest_wind .geetest_radar_tip,
.geetest_holder.geetest_wind .geetest_success_radar_tip {
  height: 37px;
}

>>> .geetest_holder.geetest_wind .geetest_success_radar_tip {
  height: 37px;
  line-height: 37px;
}

>>> .geetest_holder.geetest_wind .geetest_success_logo {
  top: 9px;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.login_name {
  font-size: 28px;
}
.forget a {
  float: right;
}
.login_content {
  display: flex;
  justify-content: center;
}
.login_content_wrap {
  display: flex;
  width: 24%;
  padding: 80px 0 50px 0;
}
.login_content_wrap_left {
  flex: 1;
}
.login_inp_sty {
  width: 100%;
}
.login_inp_top {
  line-height: 30px;
  font-size: 15px;
}
.login_inp_warp {
  padding: 10px 0;
}
.login_agreement {
  line-height: 33px;
}
.login_submit {
  width: 100%;
  margin-top: 20px;
}
</style>



