const serviceModule = {
  login: {
    url: '/api/v1/login',
    method: 'post'
  },
  emailCode: {
    url: '/api/v1/sendMail',
    method: 'post'
  },
  register: {
    url: '/api/v1/register',
    method: 'post'
  },
  loginVerification: {
    url: '/api/v1/geetest?t='+new Date().getTime(),
    method: 'get'
  }
}
const ApiSetting = {...serviceModule }
 
export default ApiSetting


