<template>
  <div class="layout">
        <layout>
            <i-header>
                <i-menu mode="horizontal" theme="dark" :active-name="this.$store.state.activename">
                    <router-link to="/">
                        <div class="layout-logo"></div>
                    </router-link>
                    <div class="layout-nav">
                        <router-link to="/">
                            <menu-item name="1">
                                <Icon type="md-home" />
                                主页
                            </menu-item>
                        </router-link>
                        <router-link to="/login">
                            <menu-item name="2">
                                <icon type="ios-keypad"></icon>
                                登录
                            </menu-item>
                        </router-link>
                        <router-link to="/register">
                            <menu-item name="3">
                                <icon type="ios-analytics"></icon>
                                注册
                            </menu-item>
                        </router-link>
                        <menu-item name="4">
                            <Icon type="ios-person" />
                            个人中心
                        </menu-item>
                    </div>
                </i-menu>
            </i-header>
        </layout>
    </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
        
    }
  }
}
</script>

<style scoped>
.layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
}
.layout-footer-center{
    text-align: center;
}

</style>



