<template>
  <div>
    <Header></Header>
    <i-content>
      <div class="login_content" style="height: 798px;">
        <div class="login_content_wrap">
          <div class="login_content_wrap_left">
            <!-- <div class="login_inp_warp">
                   <div class="login_inp_top">
                     国籍
                   </div>
                    <Select class="login_inp_sty" placeholder="China" size="large">
                        <Option value="China">China</Option>
                        <Option value="Hong Kong (China)">Hong Kong (China)</Option>
                    </Select>
            </div>-->
            <div class="login_inp_warp">
              <div class="login_inp_top">邮箱</div>
              <Input class="login_inp_sty" type="email" size="large" clearable v-model="email"/>
            </div>
            <div class="login_inp_warp">
              <div class="login_inp_top">邮箱验证码</div>
              <Input
                class="login_inp_sty"
                size="large"
                search
                :enter-button="emailCodeNum"
                @on-search="emailCodeFun()"
                v-model="emailCode"
              />
            </div>
            <div class="login_inp_warp">
              <div class="login_inp_top">登录密码</div>
              <Input
                class="login_inp_sty"
                type="password"
                size="large"
                clearable
                v-model="password"
              />
            </div>
            <div class="login_inp_warp">
              <div class="login_inp_top">确认密码</div>
              <Input
                class="login_inp_sty"
                type="password"
                size="large"
                clearable
                v-model="passwordTwo"
              />
            </div>
            <div class="login_agreement">
              <Checkbox v-model="single">我已阅读并同意
                <router-link to="/">《用户协议》</router-link>
              </Checkbox>
            </div>
            <Button
              class="login_submit"
              type="primary"
              :loading="loading"
              size="large"
              @click="submitBtn()"
            >
              <span v-if="!loading">注册</span>
              <span v-else>Loading...</span>
            </Button>
          </div>
          <div class="login_content_wrap_right">
            <p>国籍信息注册后不可修改，请务必如实选择。</p>
            <p>验证邮件可能会被误判为垃圾邮件，请注意查收。</p>
            <p>请妥善保存您的账号及登录密码。</p>
            <p>请勿和其他网站使用相同的登录密码。</p>
          </div>
        </div>
      </div>
    </i-content>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "./public/Header";
import Footer from "./public/Footer";
export default {
  name: "Register",
  data() {
    return {
      loading: false,
      email: "",
      emailCode: "",
      password: "",
      passwordTwo: "",
      single: false,
      emailCodeNum: "发送邮箱验证码",
      emailCodeBtn: 0,
      timeNum: 60
    };
  },
  components: {
    Header,
    Footer
  },
  methods: {
    emailCodeFun: function() {
      const emailVerify = this.$store.state.emailVerify;

      if (!this.email.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请先填写邮箱"
        });
      } else if (!emailVerify.test(this.email)) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请检查邮箱格式"
        });
      } else {
        if (this.emailCodeBtn == 0) {
          this.emailCodeBtn = 1;
          this.http(this.ApiSetting.emailCode, {
            email: this.email
          }).then(
            res => {
              this.loading = false;
              if (res.data.code === 0) {
                this.$Modal.error({
                  title: "提示",
                  scrollable: true,
                  content: "发送成功"
                });
              }
            },
            error => {
              console.log(error);
            }
          );
          this.emailCodeNum = this.timeNum + " S";
          const time = setInterval(() => {
            this.timeNum--;
            this.emailCodeNum = this.timeNum + " S";
            if (this.timeNum <= 0) {
              this.emailCodeNum = "重新发送";
              clearInterval(time);
              this.emailCodeBtn = 0;
              this.timeNum = 60;
            }
          }, 1000);
        } else {
          this.$Modal.error({
            title: "警告",
            scrollable: true,
            content: "请勿重复点击"
          });
          return false;
        }
      }
    },

    submitBtn: function() {
      const emailVerify = this.$store.state.emailVerify;
      if (!this.email.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请填写邮箱"
        });
      } else if (!emailVerify.test(this.email)) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请检查邮箱格式"
        });
      } else if (!this.emailCode.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请填写邮箱验证码"
        });
      } else if (!this.password.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请填写您的密码"
        });
      } else if (!this.passwordTwo.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "请确认密码"
        });
      } else if (this.passwordTwo.length < 6) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "密码不得低于四位"
        });
      } else if (this.password.length !== this.passwordTwo.length) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "两次填写密码不一致"
        });
      } else if (!this.single) {
        this.$Modal.error({
          title: "警告",
          scrollable: true,
          content: "用户协议未确认"
        });
      } else {
        this.loading = true;
        this.http(this.ApiSetting.register, {
          email: this.email,
          code: this.emailCode,
          password: this.password,
          confirm_password: this.passwordTwo
        }).then(
          res => {
            this.loading = false;
            if (res.data.code === 0) {
              this.loading = false;
              this.$Modal.error({
                title: "提示",
                scrollable: true,
                content: "注册成功"
              });
              this.$router.push({ path: "/login" });
            }
          },
          error => {
            console.log(error);
          }
        );
      }
    }
  },
  mounted() {
    this.$store.state.activename = "3";
  }
};
</script>

<style scoped>
@media screen and (max-width: 900px) {
  .login_content_wrap {
    width: 89.5% !important;
  }
}
.login_content {
  display: flex;
  justify-content: center;
}
.login_content_wrap {
  display: flex;
  width: 62.5%;
  padding: 80px 0 50px 0;
}
.login_content_wrap_left {
  flex: 1;
}
.login_content_wrap_right {
  flex: 1;
  margin-top: 38px;
  font-size: 14px;
  color: #9194a4;
}
.login_content_wrap_right p {
  line-height: 20px;
  padding-bottom: 29px;
}
.login_inp_sty {
  width: 80%;
}
.login_inp_top {
  line-height: 30px;
  font-size: 15px;
}
.login_inp_warp {
  padding: 10px 0;
}
.login_agreement {
  line-height: 33px;
}
.login_submit {
  width: 80%;
  margin-top: 20px;
}
</style>



